#contact {
    height: 100vh;
    position: relative;
}

.contact-bg {
    width: 20px;
    height: 100%;
    background-color: rgb(8, 70, 85);
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {
    flex: 1;
}

.c.right {
    flex: 1;
}

.c-title {
    font-size: 60px;
    width: 80%;
}

.c-info-item {
    display: flex;
    margin: 50px 0;
    font-weight: 400;
    width: 70%;
}
.c-icon{
    width: 30px;
    height: 30px;
    margin-right:20px;
}
.c-desc{
    font-weight: 400;
}
.c-right input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #000;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
.c-right textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}
.c-right button{
    border: none;
    background-color: antiquewhite;
    color: #fff;
    padding: 15px;
    cursor: pointer;
    font-weight: 500;
}
